import React from "react";
import {IoMdRefresh} from  "react-icons/io";
import {FaSettings} from  "react-icons/fa";



export const SwapComponent =()=>{
    return (
        <div className="flex items-center justify-between p-4 bg-gray-100 border border-gray-300">
            <div>
                <div className="flex items-center">
                <span className="mr-2 font-semibold">USD</span>
                <input
                    type="number"
                    className="w-20 p-1 border border-gray-400 rounded"
                    value={250}
                />
                </div>
                <div className="flex items-center">
                <span className="mr-2 font-semibold">NGN</span>
                <input
                    type="number"
                    className="w-20 p-1 border border-gray-400 rounded"
                    value={250}
                />
                </div>
                <button className="px-4 py-2 text-white bg-blue-500 rounded">
                Swap
                </button>
            </div>
      </div>)
}